import * as React from "react";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { runCloudFunction, WrappedParse as Parse } from "../vendor/ParseApp";
import Badge from "react-bootstrap/Badge";
import { Exception } from "sass";

export default function App() {
  const [message, setMessage] = React.useState("");
  const [model, setModel] = React.useState({
    type: 0,
  });

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
  };

  return (
    <Layout>
      <Container>
        <h3>Sign Up For Account</h3>
        <Form>
          <Row>
            <Col xs="12" md="6">
              <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Label>Email Address</Form.Label>
                <Form.Control
                  type="text"
                  placeholder=""
                  onChange={(v) => setModelValue("email", v.target.value)}
                />
              </Form.Group>
            </Col>
            <Col xs="12" md="6">
              <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Label>Password</Form.Label>
                <Form.Control
                  type="password"
                  placeholder=""
                  onChange={(v) => setModelValue("password", v.target.value)}
                />
              </Form.Group>
            </Col>
          </Row>

          <h4>
            <Badge
              bg={`${model.type === 0 ? "success" : "secondary"} defaultCursor`}
              onClick={() => setModelValue("type", 0)}
            >
              {" "}
              Job Seeker
            </Badge>{" "}
            <Badge
              onClick={() => setModelValue("type", 1)}
              bg={`${model.type === 1 ? "success" : "secondary"} defaultCursor`}
            >
              Recruiter
            </Badge>
          </h4>

          <Button
            variant="primary"
            onClick={() => {
              Parse.Cloud.run("createUser", model)
                .then(() => {
                  setMessage("Please verify your email address");
                })
                .catch((e: Exception) => {
                  setMessage(e?.message);
                });
            }}
          >
            Create Account
          </Button>
        </Form>
        <div>{message}</div>
      </Container>
    </Layout>
  );
}
